import { useState } from "react";
import { motion } from "framer-motion";
import { useGame } from "../context/GameContext";
import Link from "next/link";

export default function Home() {
  const { tokens, setTokens, inventory, setInventory } = useGame();
  const [currentDrop, setCurrentDrop] = useState(null);
  const [isOpening, setIsOpening] = useState(false);

  const drops = [
    { name: "Common Item", rarity: "Common", value: 5, chance: 0.60, color: "text-gray-500" },
    { name: "Rare Item", rarity: "Rare", value: 20, chance: 0.25, color: "text-gray-400" },
    { name: "Epic Item", rarity: "Epic", value: 50, chance: 0.10, color: "text-gray-300" },
    { name: "Legendary Item", rarity: "Legendary", value: 100, chance: 0.05, color: "text-gray-100" },
  ];

  const getRandomDrop = () => {
    const rand = Math.random();
    let cumulative = 0;
    for (const drop of drops) {
      cumulative += drop.chance;
      if (rand <= cumulative) return drop;
    }
    return drops[0];
  };

  const handleOpenCase = () => {
    if (isOpening) return;
    setIsOpening(true);
    setCurrentDrop(null);
    setTimeout(() => {
      const finalDrop = getRandomDrop();
      setCurrentDrop(finalDrop);
      setIsOpening(false);
    }, 3000);
  };

  const handleSaveDrop = () => {
    if (currentDrop) {
      setInventory([...inventory, currentDrop]);
      setCurrentDrop(null);
    }
  };

  const handleSellDrop = () => {
    if (currentDrop) {
      setTokens(tokens + currentDrop.value);
      setCurrentDrop(null);
    }
  };

  const scrollingDrops = Array(20)
    .fill(null)
    .map(() => drops[Math.floor(Math.random() * drops.length)]);

  // Animation variants
  const containerVariants = {
    hidden: { opacity: 0, y: 50 },
    visible: {
      opacity: 1,
      y: 0,
      transition: { duration: 0.5, ease: "easeOut" },
    },
  };

  const dropVariants = {
    hidden: { opacity: 0, scale: 0.9 },
    visible: { opacity: 1, scale: 1, transition: { duration: 0.4, ease: "easeOut" } },
  };

  return (
    <div className="min-h-screen bg-gray-900 flex flex-col items-center p-6">
      <motion.div
        className="w-full max-w-4xl"
        variants={containerVariants}
        initial="hidden"
        animate="visible"
      >
        {/* Header Section */}
        <div className="bg-gray-800 p-4 rounded-t-lg shadow-lg flex justify-between items-center">
          <h1 className="text-2xl font-bold text-gray-100">Case Opener</h1>
          <div className="flex items-center space-x-4">
            <p className="text-gray-300">
              Tokens: <span className="font-semibold text-gray-100">{tokens}</span>
            </p>
            <Link href="/inventory" className="text-gray-400 hover:text-gray-200 transition">
              Inventory
            </Link>
          </div>
        </div>

        {/* Main Content */}
        <div className="bg-gray-700 p-6 rounded-b-lg shadow-lg flex flex-col items-center">
          <motion.button
            onClick={handleOpenCase}
            className="bg-gray-500 text-white px-6 py-3 rounded-lg hover:bg-gray-600 transition disabled:opacity-50"
            disabled={isOpening}
            whileHover={{ scale: 1.05 }}
            whileTap={{ scale: 0.95 }}
          >
            Open Free Case
          </motion.button>

          {/* Scrolling Animation */}
          {isOpening && (
            <div className="mt-6 w-full h-24 overflow-hidden relative border border-gray-500 rounded-lg">
              <motion.div
                className="flex space-x-4"
                initial={{ x: "100%" }}
                animate={{ x: "-100%" }}
                transition={{ duration: 3, ease: "linear" }}
              >
                {scrollingDrops.map((drop, index) => (
                  <div
                    key={index}
                    className={`flex-shrink-0 w-20 p-2 text-center ${drop.color} bg-gray-600 rounded border border-gray-500`}
                  >
                    {drop.name}
                  </div>
                ))}
              </motion.div>
              <div className="absolute inset-0 flex items-center justify-center pointer-events-none">
                <div className="w-1 h-20 bg-gray-400"></div>
              </div>
            </div>
          )}

          {/* Drop Result */}
          {currentDrop && !isOpening && (
            <motion.div
              className="mt-6 w-full max-w-sm bg-gray-600 p-4 rounded-lg border border-gray-500"
              variants={dropVariants}
              initial="hidden"
              animate="visible"
              whileHover={{ scale: 1.05, boxShadow: "0 4px 15px rgba(0, 0, 0, 0.3)" }}
            >
              <div className="w-16 h-16 bg-gray-800 rounded mx-auto mb-2 flex items-center justify-center">
                <span className="text-gray-400 text-sm">Icon</span>
              </div>
              <h2 className="text-xl font-semibold text-gray-100">You Got:</h2>
              <p className={`text-lg my-2 ${currentDrop.color}`}>
                {currentDrop.name} ({currentDrop.rarity}, {currentDrop.value} tokens)
              </p>
              <div className="flex justify-center space-x-4 mt-4">
                <motion.button
                  onClick={handleSaveDrop}
                  className="bg-gray-500 text-white px-4 py-2 rounded hover:bg-gray-600 transition"
                  whileHover={{ scale: 1.05 }}
                  whileTap={{ scale: 0.95 }}
                >
                  Save to Inventory
                </motion.button>
                <motion.button
                  onClick={handleSellDrop}
                  className="bg-gray-600 text-white px-4 py-2 rounded hover:bg-gray-700 transition"
                  whileHover={{ scale: 1.05 }}
                  whileTap={{ scale: 0.95 }}
                >
                  Sell Drop
                </motion.button>
              </div>
            </motion.div>
          )}
        </div>
      </motion.div>
    </div>
  );
}